/**
 * Detection of Freesat by checking the userAgent for "freesat". Return true if the
 * user agent contains that term. Below is a sample user agent string extracted
 * from a Freesat device.
 *
 * "Mozilla/5.0 (Linux aarch64) AppleWebKit/537.36 (KHTML, like Gecko)
 * Chrome/67.0.3396.99 Safari/537.36 OPR/46.0.2207.0 OMI/4.13.6.465.Charlie.140
 * HbbTV/1.5.1 (+DRM; ARRIS; FS-ARS-01A; 5; ; com.arris.FS-ARS-01;)
 * freesat/3.0 (1.0.6) Freesat_STB_BCM72604_2019_ARRIS/5 (ARRIS,FS-ARS-01A,Wired)"
 * @module freesat/detection
 */

import { detection as detect } from '@accedo/xdk-core';

const isFreesat = (...args) => {
    const userAgent = args.length > 0 && args[0] !== undefined ? args[0] : '';
    return userAgent.toLowerCase().indexOf('freesat') !== -1;
};

const detection = detect(isFreesat);

export default detection;
